// 聊天室画面
<template>
  <div id="message">
    <div class="title">聊天室</div>
      <div class="header">
        文明上网，净化网络
      </div>
      <div class="contentNew">
      <div class="messages" id="con" ref="QAContent">

          <div v-for="(image, index) in Message" :key="index"  class="avatar-container">

              <el-tooltip effect="dark" :content="showMyself(image.id,image.name)" placement="left">
              <img v-if="image.avatar!=''&&image.status==0"  :src="image.avatar"
             style="width: 40px;height:40px;border-radius: 50%;border: 1px solid #409EFF;"/>
            </el-tooltip>
            <img v-if="image.avatar==''&&image.status==0"  style="width: 40px;border-radius: 50%;"/>
            <div @mouseenter="mouseEnterHandler(index)" @mouseleave="mouseLeaveHandler(index)">

              <span v-if="image.name!='系统时间'&&image.status==0" >
                <div style="border-radius: 5px;width: 500px;word-wrap:break-word; word-break:break-all; overflow: hidden;"
                v-if="checkMyself(image.id)">
                  <!-- <el-popover
                    placement="right-start"
                    width="150px"
                    trigger="hover">
                    <el-button @click="goback(image.num,image.time)">撤回</el-button>
                    <el-button @click="delmsg">删除</el-button>
                    <div slot="reference" style="border-radius: 2px;"> {{ replace(image.message) }}  </div> 
                  </el-popover> -->
                  <el-dropdown trigger="click" size="small" >
                    <span class="el-dropdown-link">
                      {{ replace(image.message) }}
                      <div> {{ replace(image.commentMessage) }}</div>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                      <el-dropdown-item icon="el-icon-caret-left" @click.native="goback(image.num,image.time)">撤回</el-dropdown-item>
                      <el-dropdown-item icon="el-icon-s-fold" @click.native="comment(image.num,image.name,image.message,image.time)">回复</el-dropdown-item>
                    </el-dropdown-menu>
                  </el-dropdown>
                </div>
                <div style="border-radius: 5px;width: 500px;word-wrap:break-word; word-break:break-all; overflow: hidden;"
                v-if="!checkMyself(image.id)">
                  <!-- <el-popover
                    placement="right-start"
                    width="150px"
                    trigger="hover">
                    <el-button @click="delmsg">删除</el-button>
                    <div slot="reference" style="border-radius: 2px;"> {{ replace(image.message) }}  </div> 
                  </el-popover> -->
                  <el-dropdown trigger="click" size="small">
                    <span class="el-dropdown-link">
                      {{ replace(image.message) }}
                      <div> {{ replace(image.commentMessage) }}</div>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                      <el-dropdown-item icon="el-icon-s-fold" @click.native="comment(image.num,image.name,image.message,image.time,image.status)">回复</el-dropdown-item>
                    </el-dropdown-menu>
                  </el-dropdown>
                </div>
              </span>
              <span v-if="image.name!='系统时间'&&image.status==1" style="margin-left: 350px;">
                 {{image.name}} 撤回一条消息
                 <span @click="edit(image.message,image.commentMessage,image.parentId)" class="edit" >点我编辑</span>
              </span>
              <span v-if="image.name=='系统时间'" style="margin-left: 300px;">{{ replace(image.message) }}</span>
            </div>
            <sup style="font-size: 12px;margin-left: 200px;"  ><span  :id="change(index)" class="timeData">{{ image.time }}</span></sup>
       


          </div>

      </div>
      <picker id="emoji" :include="['people','Smileys']" :showSearch="false" :showPreview="false"  :showCategories="false" @select="addEmoji" />
      <div class="foot">
        <div class="comment" id="comment">
            {{ commentContent }}
            

           <i class="el-icon-close" @click="clearComment"></i>
       
        </div>
        <el-input v-model="input" placeholder="请输入内容" style=
          "width: 900px;margin-left: 40px;position: absolute;" @keyup.enter.native="sendMsg">
          <i slot="suffix" @click="showEmoji" class="el-icon-circle-plus" id="send1" style="font-size: 30px;margin-top: 4px;padding-top: 1px;"></i>
          <i slot="suffix" @click="sendMsg" class="el-icon-s-promotion" id="send" style="font-size: 30px;margin-top: 3px;"></i>
        </el-input>
      </div>
      </div>
    </div>

</template>

<script>
import { Picker } from "emoji-mart-vue"; //引入组件
export default {
  components: { //注册组件，不能全局挂载
    Picker
  },
  // name: 'message'
  data() {
    return {
      input:'',
      Message:[],
      sendMsgloading :null,
      commentContent:'',
      tempNum:null
    }
  },
  created() {   //0表示正常聊天，1表示撤回，2表示删除，3表示全部信息查询,4表示下线
    this.init("更新信息",3,null,null)
    this.goEnd()
  },
  methods: {
  edit(message,commentMessage,num){
    if(commentMessage=='')
    {
      this.tempNum=num
      document.getElementById("comment").style.display="none"
    }
    
    else
    {
      this.tempNum=num
      this.commentContent=commentMessage
      this.input=message
      document.getElementById("comment").style.display="block"
    }
  },
    clearComment(){
      document.getElementById("comment").style.display="none"
    },
    checkMess(status){
      if(status!=0)
        return false;
      return true;
    },
    checkMyself(id){
      if(this.$jwtDecode(sessionStorage.getItem("token")).id!=id)
        return false;
      return true;
    },
    comment(num,name,message,time,status){//回复功能

      this.commentContent=name+":"+message
      this.tempNum=num
      document.getElementById("comment").style.display="block"
    },
    goback(num,time){
      //时间样式 "2024-02-02 19:23:34"
      let date = new Date();  
      let now = date.toLocaleString('zh-CN', {  
        year: 'numeric',  
        month: '2-digit',  
        day: '2-digit',  
        hour: '2-digit',  
        minute: '2-digit',  
        second: '2-digit',  
      });  
        //撤回，云端没有删除，只是做了记号
      if(String(time).split(" ")[0]==String(now).replaceAll("/","-").split(" ")[0])
      {
        if(String(time).split(" ")[1].split(":")[0]==String(now).replaceAll("/","-").split(" ")[1].split(":")[0])
        {
          var nowMinute=parseInt(String(now).replaceAll("/","-").split(" ")[1].split(":")[1])
          var lastMinute=parseInt(String(time).split(" ")[1].split(":")[1])
          if(nowMinute-lastMinute>2)
          {
            this.$message.warning("只能撤回2分钟内的消息")
          }else{
            this.init(num,1,null,null);
          }
        }else{
          this.$message.warning("只能撤回2分钟内的消息")
        }
      }else{
        this.$message.warning("只能撤回2分钟内的消息")
      }


    },
    delmsg(){
      //删除只能删除本地的,其实云端没有删除

    },
    showMyself(id,name){
      if(this.$jwtDecode(sessionStorage.getItem("token")).id!=id)
        return name;
      else
        return '我';
    },
    showEmoji(){
      if(document.getElementById("emoji").style.display=='flex')
      {
        document.getElementById("emoji").style.display='none'
      }
      else
      {
        document.getElementById("emoji").style.display='flex'
        document.getElementById("emoji").style.width='215px'
      }
    },
    addEmoji(e){

      this.input += e.native;
      document.getElementById("emoji").style.display='none'
    },
    //敏感词替换，后台数据不替换，前端展示时替换
    replace(str){
      //敏感词汇
      var sensitive=['傻逼','操','三八','色','逼','鸡巴','jb','sb','b','cnm',
    '贱货','傻']
      for(var i=0;i<sensitive.length;i++)
      {
        if(str.includes(sensitive[i]))
          str=str.replace(sensitive[i],this.fromLen(sensitive[i]))
      }
      return str
    },
    fromLen(str){
      var result=''
      for(var i=0;i<str.length;i++)
        result+='*'
      return result;
    },
    change(index){
      return "timeData"+index;
    },
    goEnd(){
      this.$nextTick(() => {
          let div = document.querySelector('#con');
          div.scrollTop = div.scrollHeight;
      })
    },
    mouseEnterHandler(index) {
      // 当鼠标移入图片时触发的处理函数
     var str="timeData"+index;
     document.getElementById(str).style.display='block'

    },
    mouseLeaveHandler(index) {
      // 当鼠标移出图片时触发的处理函数
     var str="timeData"+index;
     document.getElementById(str).style.display='none'
    },
    init(mess,type,sender,receiver){
      let ws=new WebSocket(this.$websocketChatUrl+this.$jwtDecode(sessionStorage.getItem("token")).id)
      //发送数据
      let msobj={}
      if(type!=2){
        msobj.sender='客户端'
        msobj.receiver='服务器'
        msobj.message=mess
        msobj.type=type
      }else{
        msobj.sender=sender
        msobj.receiver=receiver
        msobj.message=mess
        msobj.type=type
      }
   
      // console.log(ws.readyState)
      if (ws.readyState === 1) { // this.websock.readyState = 1 表示连接成功，可以立即发送信息
        ws.send(window.JSON.stringify(msobj))
      } else if (ws.readyState === 0) { // 表示正在连接，设置300ms后发送信息
        setTimeout(function () {
          ws.send(window.JSON.stringify(msobj))
        }, 300);
      } else { // 连接未创建或者创建失败，则重新创建连接，并设置500ms后发送信息
        setTimeout(function () {
          ws.send(window.JSON.stringify(msobj))
        }, 500);
      }
      //接收数据
      ws.onmessage=(msg)=>{
        //处理将数据打印在前端的功能
        // console.log(JSON.parse(msg.data))
        this.Message=JSON.parse(msg.data)
        this.goEnd()
        this.LoadingClose()
      }
    },
    LoadingOpen(){
      this.sendMsgloading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.07)'
        });
    },
    LoadingClose(){
      if(this.sendMsgloading!=null)
        this.sendMsgloading.close()
    },
    sendMsg(){
      if(this.input==''){
        this.$message.warning("不能发送空的消息");
      }else{
        if(document.getElementById("comment").style.display=="block")
        {
          //评论
          this.LoadingOpen()
          this.init(this.input,2,this.tempNum,this.commentContent)
         
          this.input=''
          this.goEnd()
          document.getElementById("comment").style.display="none"
        }else{
          //正常的消息
          this.LoadingOpen()
          this.init(this.input,0,null,null)
          this.input=''
          this.goEnd()
        }

      }
    },
  }
}

</script>



<style lang="less" scoped>
.edit{
  color: blue;
}
.edit:hover{
  color: red;
}
#emoji{
  display: none;
  margin-left: 940px;
}
.comment {
  margin-left: 40px;
  display: none;
}

.comment > i {
  margin-left: 200px;
}

.comment > i:hover {
  color: #409EFF;
}
/deep/ .emoji-mart-category-label span[data-v-376cda0e]{
  display: none ;
}

/deep/ .emoji-mart[data-v-7bc71df8] {
  font-family: -apple-system, BlinkMacSystemFont, "Helvetica Neue", sans-serif;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  height: 150px;
  width: 250px;
  border: 1px solid #d9d9d9;
  border-radius: 5px;
  background: #fff;
  position: absolute;
}

.timeData{
  display: none;
}

#send:hover{
 color: #409EFF;
}
#send1:hover{
 color: #409EFF;
}

.avatar-container {
    // background-image: url("../../assets/img/evening.png");
    // background-size: 100% 100%;
    display: flex;
    align-items: center; /* 如果需要垂直居中 */
    margin-bottom: 10px;
}
.timeData{
  display: none;
}
.avatar-container img {
    float: left; /* 让图片浮动到左边 */
    margin-right: 10px; /* 添加一些间距 */
}
.contentNew{
  background-color: #d9d9d9;
  height: 510px;
}
.messages{
  margin-left: 40px;
  // background-color: blue;
  width: 96%;
  height: 420px;
  // overflow: auto;
  overflow-y: scroll; /* 只显示垂直滚动条 */
  position: relative; /* 确保滚动条位于正确的位置 */

}
.foot{
  margin-top: 10px;
  // border: 1px solid #409EFF;
  // border-radius: 10px;
  height: 80px;

}

.pagination {
  display: flex;
  justify-content: center;
}
#message {
  width: 980px;
  margin: 0 auto;
}
.title {
  margin: 20px;
}
.content {
  padding: 20px 0px;
}
</style>


